.has-small-font-size {
  display: none !important;
}

.title {
  line-height: 110px;
  font-weight: 700;
  letter-spacing: 1px;
}

.subtitle {
  line-height: 24px;
  font-weight: 400;
  font-family: "Poppins";
  margin: 2em 0;
}

.btn-layout {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.7rem 4rem;
  font-weight: 400;
  font-size: 19px;
  text-align: center;
  margin-bottom: 2em;
  width: 13em;
  color: #FFFFFF !important;
  text-decoration: none !important;
  background-color: #0e90ed;
}

.layout-2-content {
  height: 51vw;
}

.pwidth {
  width: 37em;
}
